body {
  font-family: 'Josefin Sans';
  background-color: #FFFAFB;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;

}

::-webkit-scrollbar {
  width: 0px;
}

.right-arrow,
.left-arrow {
  cursor: pointer;
  background: transparent;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FF2625;
  font-size: 25px;
  border-radius: 4px;
  position: absolute;
  bottom: -20px;
  right: 80px;
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.right-arrow {
  right: 140px;
}

.right-arrow:hover,
.left-arrow:hover {
  transform: scale(1.3, 1.3);
}

.react-horizontal-scrolling-menu--wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.detail-image {
  width: 729px;
  height: 742px;
}

.hero-banner-img {
  position: absolute;
  right: 0px;
  z-index: -1000;
  top: 140px;
  width: 700px;
  height: 900px;
  margin-top: -270px;
}

.exercise-card {
  width: 300px;
  max-height: 345px;
  background: #fff;
  /* border-top: 4px solid #FF2625; */
  border-bottom-left-radius: 20px;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-bottom: 10px;
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.exercise-card img {
  height: 326px;
}

.bodyPart-card {
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.classForCardHoverOnly {
  margin: 2rem;
}

.classForCardHoverOnly:hover,
.exercise-card:hover,
.bodyPart-card:hover {
  transform: scale(1.1, 1.1);
  z-index: 100000;
}

.search-btn:hover {
  color: #FF2625 !important;
  border: 1px solid #FF2625 !important;
}

.exercise-video {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 387px;
  height: 381px;
  text-decoration: none;
}


@media screen and (max-width:800px) {
  .exercise-card {
    width: 280px;
  }

  .hero-banner-img {
    display: none;
  }
}

@media screen and (min-width:800px) and (max-width:1200px) {
  .hero-banner-img {
    position: absolute;
    right: 0px;
    z-index: -1000;
    top: 0px;
    width: 500px;
    height: auto;
    margin-top: -130px;
  }

}

@media screen and (max-width:1200px) {
  .detail-image {
    width: 300px;
    height: 300px;
  }


  .react-horizontal-scrolling-menu--scroll-container {
    width: 500px;
  }

  .left-arrow,
  .right-arrow {
    position: static !important;
  }

  /* .hero-banner-img {
    display: none;
  } */

  .exercise-card {
    width: 320px;
  }

  .exercise-video {
    width: 320px;
    height: 300px;
  }
}

.spinner {
  display: flex;
  justify-content: center;
  width: 100%;
}

.spinner::after {
  content: " ";
  width: "200px";
  height: "200px";
  border: 20px solid #f3f3f3;
  border-top: 20px solid #FF2625;
  border-radius: 100%;
  animation: loading 1s ease infinite;
}

@keyframes loading {
  to {
    transform: rotate(1turn);
  }
}